import React from "react";
import ReactDOM from "react-dom";

import "./App.css";
// Needed for onTouchTap: http://stackoverflow.com/a/34015469/988941
import injectTapEventPlugin from "react-tap-event-plugin";

import {
  Card,
  CardActions,
  CardHeader,
  CardMedia,
  CardTitle,
  CardText
} from "material-ui/Card";

import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";

injectTapEventPlugin();

const data = [
  {
    name: "Front view",
    fn: "front-view-2.jpg",
    desc: "46 Maquoit sits on beautiful Maquoit Bay, 10 minutes from the LL Bean Paddling center.",
    transform: ""
  },
  {
    name: "Living Room",
    fn: "living-room-2.jpg",
    desc: "The living room faces the water and features a working double-sided wood burning fireplace.",
    transform: "rotate(180deg)"
  },
  {
    name: "Kitchen",
    fn: "kitchen.jpg",
    desc: "A fully stocked chef's kitchen with retro appliances.",
    transform: ""
  },
  {
    name: "Dining room",
    fn: "dining-room-2.jpg",
    desc: "The dining room door opens completely, creating an outdoor feeling inside.",
    transform: ""
  },
  {
    name: "Master bedroom",
    fn: "master-bedroom.jpg",
    desc: "The master bedroom has french doors that open to a back deck overlooking the bay.",
    transform: "rotate(180deg)"
  },

  {
    name: "Master bath",
    fn: "master-bath.jpg",
    desc: "The master path features a two person shower and soaking tub.",
    transform: "rotate(180deg)"
  },
  {
    name: "Guest bedroom",
    fn: "guest-bedroom.jpg",
    desc: "The guest bedroom has french doors that open to a back deck overlooking the bay.",
    transform: "rotate(180deg)"
  },
  {
    name: "Sunset",
    fn: "sunset.JPG",
    desc: "Close to another beautiful day.",
    transform: "rotate(180deg)"
  }
];

export default class App extends React.Component {
  render() {
    var cards = data.map(pic => {
      console.log(pic);
      return (
        <Card style={{ marginBottom: "20px" }}>
          <CardMedia
            overlay={<CardTitle title={pic.name} subtitle={pic.desc} />}
          >
            <img
              style={{ transform: pic.transform, width: "80%" }}
              src={"images/" + pic.fn}
              alt=""
            />
          </CardMedia>
        </Card>
      );
    });
    console.log(cards);
    return (
      <MuiThemeProvider>
        <div style={{ padding: "50px" }}>
          <h1>46 Maquoit</h1>
          {cards}
        </div>
      </MuiThemeProvider>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));



